import {Component, OnInit} from '@angular/core';
import {AuthService} from 'src/app/common/services/auth.service';
import {ProyectoService} from 'src/app/common/services/proyecto.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  gameToHTML(game: any) {
    return `
    <div class="media">
        <img style="width: 30px; height: 30px;"
        src="${game.image}"
        class="mr-3" alt="...">
        <div class="media-body border-1">
        <h5 class="mt-0">${game.nombre}</h5> Puntaje: ${game.clas}
        </div>
    </div>
    `
  }

  gameListToHTML(list: any, id: any) {
    if (id && list && document.getElementById(id)) {
      (document.getElementById(id) as HTMLElement).innerHTML = list.map(this.gameToHTML).join('');
    }
  }

  carouselToHTML(tourney: any) {
    return `
     <div class="${tourney.pos === 1 ? "carousel-item active" : "carousel-item"}">
          <img src="${tourney.image}" class="d-block w-100" alt="...">
          <div class="carousel-caption d-none d-md-block">
              <span class="d-block p-2 bg-dark text-white">
                  <h5>${tourney.nombre}</h5>
                  <p>${tourney.juego}</p>
                  <p>${tourney.fechai}</p>
                  <a href="../html/oneTourney.html?id=${tourney._id}" type="button" class="btn btn-primary btn-lg btn-block">Ver Torneo</a>
              </span>
          </div>
      </div>`
  }

  loggedIn: boolean = false;

  constructor(private authService: AuthService, private proyectoService: ProyectoService) {
  }

  ngOnInit(): void {
    this.authService.loginStatus.subscribe(status => {
      this.loggedIn = status;
    })

    this.proyectoService.getTourneys().then(result => {
      const listTourneys = result.content;
      (document.getElementById('tourneysDestacados') as HTMLElement).innerHTML = listTourneys.map(this.carouselToHTML).join('');
    }).catch(err => {
      console.log("error en lista de Torneos: ", err);
    })

    this.proyectoService.getTourneys2().then(result => {
      const listTourneys = result.content;
      (document.getElementById('tourneysRecientes') as HTMLElement).innerHTML = listTourneys.map(this.carouselToHTML).join('');
    }).catch(err => {
      console.log("error en lista de Torneos: ", err);
    })

    this.proyectoService.getTourneys3().then(result => {
      const listTourneys = result.content;
      (document.getElementById('tourneysProximos') as HTMLElement).innerHTML = listTourneys.map(this.carouselToHTML).join('');
    }).catch(err => {
      console.log("error en lista de Torneos: ", err);
    })

  }

}
