import { Injectable } from '@angular/core';

import { HttpClient,HttpHeaders } from "@angular/common/http";
import { environment } from 'src/environments/environment';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class ProyectoService {


  constructor(
    private httpClient: HttpClient,
    private authService: AuthService) {}

  getUserbyId(uid: string):Promise<any>{
    const hedersIn: HttpHeaders = new HttpHeaders({
      'content-type': 'application/json',
      'x-auth-user': this.authService.getToken()
    })
    const url = `${environment.apiUrl}api/users/${uid}`;
    return this.httpClient.get(url,{headers: hedersIn}).toPromise();
  }

  getTourneys():Promise<any>{
    const hedersIn: HttpHeaders = new HttpHeaders({
      'content-type': 'application/json',
      'x-auth-user': this.authService.getToken()
    })
    const url = `${environment.apiUrl}api/tourneys/carousel?order=highlighted&limit=5`;
    return this.httpClient.get(url,{headers: hedersIn}).toPromise();
  }

  getTourneys2():Promise<any>{
    const hedersIn: HttpHeaders = new HttpHeaders({
      'content-type': 'application/json',
      'x-auth-user': this.authService.getToken()
    })
    const url = `${environment.apiUrl}api/tourneys/carousel?order=old&limit=5`;
    return this.httpClient.get(url,{headers: hedersIn}).toPromise();
  }

  getTourneys3():Promise<any>{
    const hedersIn: HttpHeaders = new HttpHeaders({
      'content-type': 'application/json',
      'x-auth-user': this.authService.getToken()
    })
    const url = `${environment.apiUrl}api/tourneys/carousel?order=next&limit=5`;
    return this.httpClient.get(url,{headers: hedersIn}).toPromise();
  }

  getTourneysFilter(filter: string, page: number):Promise<any>{ 
    const hedersIn: HttpHeaders = new HttpHeaders({
      'content-type': 'application/json',
      'x-auth-user': this.authService.getToken()
    })
    const nfilter = (filter) ? `${filter}` : '';
    const url = `${environment.apiUrl}api/tourneys?page=${page}&limit=3${nfilter}`;
    return this.httpClient.get(url,{headers: hedersIn}).toPromise();
  }

  updateTourney(id: string, data: any):Promise<any>{
    const hedersIn: HttpHeaders = new HttpHeaders({
      'content-type': 'application/json',
      'x-auth-user': this.authService.getToken()
    })

    const url = `${environment.apiUrl}api/tourneys/${id}`;
    return this.httpClient.put(url,data,{headers: hedersIn}).toPromise();
  }

  getTourney(id: string):Promise<any>{
    const hedersIn: HttpHeaders = new HttpHeaders({
      'content-type': 'application/json',
      'x-auth-user': this.authService.getToken()
    })

    const url = `${environment.apiUrl}api/tourneys/${id}`;
    return this.httpClient.get(url,{headers: hedersIn}).toPromise();
  }
}
